<template>
  <div class="log" >
    <div class="top" v-bind:style="{height:topHeight,width:topWidth}">
      <img class="logo" src="../../static/img/padLoGo.png">
      <div class="login" >
        <span class="logText">胸痛中心管理平台</span>
        <div class="accountNumber">
          <input  id="account" maxlength ="20" name="username" v-model="logindata.username" @keyup.enter="login" type="text" placeholder="xtkf001">
        </div>
        <div class="passWord">
          <input  id="passwords" maxlength ="20" name="password" v-model="logindata.password" @keyup.enter="login" type="password" placeholder="0">
        </div>
        <div class="passwordForget">
          <p style="font-size: 14px;float:left;" class="checkbox">
            <Checkbox v-model="rememberpwd" >记住密码</Checkbox>
            <!-- <input class="check" style="margin-left:0px;margin-top:4px;"
                    type="checkbox" name="renumber" @keyup.enter="login">-->
            <!--<span style="display: inline-block;margin-left:-5px;">记住密码</span>-->
          </p>
          <!--<p style="cursor:pointer;float:right;">-->
            <!--<a class="forgetpwd" herf="" @click="forgetPwd">忘记密码</a>-->
          <!--</p>-->
        </div>
        <div class="loginDiv">
          <button class="loginButton" type="submit" @keyup.enter="login" @click="login"><span>登&nbsp;&nbsp;&nbsp;录</span></button>
        </div>
      </div>
      <!--</div>-->
    </div>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  name: "default",
  components: {},
  data() {
    return {
      logindata: {
        username: "xtkf001",
        password: "0",
        userSource: ""
      },
      topWidth:"",
      topHeight: "",
      rememberpwd:false,
      menuList: []
    };
  },
  created: function() {
    //判断 pc pad mpbile
    //this.browserRedirect();
    //alert(this.logindata.userSource)
  },
  mounted() {
    $(window).resize(function() {
      //this.toptHeight=$(window).height()-100+"px";
      this.topHeight = $(window).height()+ "px";
      this.topWidth = $(window).width()+ "px";
    });
    //this.toptHeight=$(window).height()-100+"px";
    this.topHeight = $(window).height()+ "px";
    this.topWidth = $(window).width()+ "px";
    if (localStorage.getItem("remember")) {
      this.logindata = JSON.parse(localStorage.getItem("remember"));
      this.rememberpwd = true;
    }
    //alert(this.toptHeight);
  },
  methods: {
//    changepwd(){
//      let _this=this
//      if(this.rememberpwd){ //选中 true 记住密码
//        localStorage.setItem('remember', JSON.stringify(_this.logindata))
//      }else{
//        localStorage.removeItem('remember')
//      }
//      // console.log(sessionStorage.getItem('remember'))
//    },
    login() {
      let _this = this;
      // console.log(_this.util.logindata)
    // this.$http.post(_this.host('login')+"?username="+this.logindata.username+"&password="+this.logindata.password+"&userSource="+this.logindata.userSource)//
    this.$http.post(_this.host('login')+"?username="+_this.logindata.username+"&password="+_this.logindata.password+"&userSource=PC")
      .then(function (response) {
        if(response.data.state=="ok"){
          this.$http.post(_this.host('getUserInfo'),{})
            .then(function (response) {
              if(response.data.state=="1"){
                _this.util.logindata=response.data.results[0];
                // sessionStorage.setItem('logindata', JSON.stringify(response.data.results[0]))
               this.changepwd();
                console.log(_this.util.logindata)
                _this.$router.push("/home");
                // _this.changepwd()
                // sessionStorage.setItem('headermenu',0);
                // //根据获取菜单进行跳转；
                // _this.allNav(_this.util.logindata.pkUser,_this.util.logindata.userType);
                //建立websocket服务连接
                this.location.initWebSocket();
              }
              if(response.data.state==2){
                // _this.$toast.center('warn.png',response.data.msg)
                _this.$router.push("/");
              }
            })
            .catch(function (error) {
              console.log(error);
            });
        }else{
          _this.$toast.center('fail.png',response.data.msg)
        }
      })
      .catch(function (error) {
        _this.$toast.center('fail.png',error)
        console.log(error);
      });
      // this.$router.push({
      //   path: "/home"
      // });
    },

//     login() {
//       let _this = this,
//         pathName;
//       if (
//         this.logindata.username == "medex" &&
//         this.logindata.password == "123"
//       ) {
//         pathName = "/home";
//       } else {
//         _this.$toast.center("fail.png", "用户名或密码错误!");
//       }
//       _this.$router.push({
//         path: pathName
//       });
//     },

    changepwd() {
      let _this = this;
      if (this.rememberpwd) {
        //选中 true 记住密码
        localStorage.setItem("remember", JSON.stringify(_this.logindata));
      } else {
        localStorage.removeItem("remember");
      }
      // console.log(sessionStorage.getItem('remember'))
    },
    forgetPwd() {
      this.$toast.center("warn.png", "请联系管理员");
    },
    //查询一级菜单导航
    allNav(pkuser, userType) {
      let _this = this;
      this.$http
        .post(_this.host("menu/listByUser"), { pkUser: pkuser })
        .then(function(response) {
          //console.log(response.data);
          _this.menuList = [];
          let dataJson = response.data;
          if (response.data.state == "1") {
            var data1 = [];
            response.data.results.forEach(function(item, index) {
              if (item.pkParent == "" || item.pkParent == null) {
                data1.push(item);
              }
            });
            // console.log(data1);
            if (userType == 0 || userType == -1) {
              _this.renderMenu(
                data1,
                "0001A0100000000000UL",
                require("../../static/img/navActive/system.png"),
                require("../../static/img/navInactive/system.png")
              ); //系统设置
            } else {
              _this.renderMenu(
                data1,
                "1001A0100000002QWJ0H",
                require("../../static/img/navActive/order.png"),
                require("../../static/img/navInactive/order.png")
              ); //预约管理
              _this.renderMenu(
                data1,
                "0001A0100000000000UN",
                require("../../static/img/navActive/patient.png"),
                require("../../static/img/navInactive/patient.png")
              ); //患者管理
              _this.renderMenu(
                data1,
                "1001A0100000003AGT6H",
                require("../../static/img/navActive/patientList.png"),
                require("../../static/img/navInactive/patientList.png")
              ); //设备列表
              _this.renderMenu(
                data1,
                "1001A0100000002QWVCX",
                require("../../static/img/navActive/statistics.png"),
                require("../../static/img/navInactive/statistics.png")
              ); //统计查询
              _this.renderMenu(
                data1,
                "1001A0100000002QX1J5",
                require("../../static/img/navActive/movement.png"),
                require("../../static/img/navInactive/movement.png")
              ); //运动执行
              _this.renderMenu(
                data1,
                "1001A0100000002QXBKQ",
                require("../../static/img/navActive/assess.png"),
                require("../../static/img/navInactive/assess.png")
              ); //患者评估
              _this.renderMenu(
                data1,
                "0001A0100000000000UL",
                require("../../static/img/navActive/system.png"),
                require("../../static/img/navInactive/system.png")
              ); //系统设置
            }
            sessionStorage.setItem("menuList", JSON.stringify(_this.menuList));
            //console.log(_this.menuList);
            /* console.log(_this.menuList[0].to);
              console.log(_this.menuList.length);*/
            if (_this.menuList.length == 0) {
              _this.$toast.center("warn.png", "未分配权限，请联系管理员");
              /*this.$router.push({
                 name: "indexvue",
               });*/
            } else {
              _this.$router.push({
                path: _this.menuList[0].to
              });
              _this.$toast.center("success.png", "登录成功");
            }
          } else {
            let backjson = { state: dataJson.state, msg: dataJson.msg };
            _this.backLogin(backjson);
          }
        })
        .catch(function(error) {
          _this.$toast.center("fail.png", "菜单获取异常");
        });
    },
    //排序渲染一级菜单
    renderMenu(data, menuId, imgActive, imgInActive) {
      let _this = this;
      data.forEach(function(item, index) {
        // console.log(item);
        if (item.pkMenu == menuId && item.htmlLink != null) {
          _this.menuList.push({
            to: item.htmlLink,
            text: item.name,
            active: imgActive,
            inactive: imgInActive
          });
        }
      });
    },
    //判断硬件环境
    browserRedirect() {
      let _this = this;
      var os = (function() {
        var ua = navigator.userAgent,
          isWindowsPhone = /(?:Windows Phone)/.test(ua),
          isSymbian = /(?:SymbianOS)/.test(ua) || isWindowsPhone,
          isAndroid = /(?:Android)/.test(ua),
          isFireFox = /(?:Firefox)/.test(ua),
          isChrome = /(?:Chrome|CriOS)/.test(ua),
          isMobile = /(?:Mobile)/.test(ua),
          isTablet =
            /(?:iPad|PlayBook)/.test(ua) ||
            (isAndroid && !/(?:Mobile)/.test(ua)) ||
            (isFireFox && /(?:Tablet)/.test(ua)),
          isPhone = /(?:iPhone)/.test(ua) && !isTablet,
          isPc = !isPhone && !isAndroid && !isSymbian;
        //alert(isTablet+'++++'+isPhone+'++++'+isAndroid+'++++'+isPc+'++++'+isMobile)
        return {
          isTablet: isTablet,
          isPhone: isPhone,
          isAndroid: isAndroid,
          isPc: isPc,
          isMobile: isMobile
        };
      })();
      //console.log(os)
      if ((os.isAndroid && os.isMobile) || os.isPhone) {
        _this.logindata.userSource = "MOBILE";
        //alert("手机" );
      } else if (os.isTablet) {
        _this.logindata.userSource = "PAD";
        //alert("平板" );
      } else if (os.isPc) {
        _this.logindata.userSource = "PC";
        //alert("电脑" );
      }
    }
  }
};
</script>

<style scoped lang="css">
@import url("../styles/login.css");
</style>



